"use client";
import { TextField } from "@mui/material";

const CustomInput = ({
  label,
  name,
  type = "text",
  value,
  onChange,
  required,
  disabled,
}) => (
  <TextField
    size="small"
    variant="standard"
    label={label}
    name={name}
    type={type}
    value={value || ""}
    onChange={(e) => onChange(name, e.target.value)}
    required={required}
    disabled={disabled}
    InputLabelProps={type === "date" ? { shrink: true } : {}}
    fullWidth
    margin="dense"
    sx={{
      width: "250px", // ✅ uniform width
      "& .MuiInputBase-root": {
        fontSize: "0.875rem",
        paddingBottom: "2px",
      },
      "& .MuiInputBase-input": {
        padding: "6px 0 4px",
        fontSize: "0.875rem",
      },
      "& input[type='date']": {
        appearance: "none",
        WebkitAppearance: "none",
        MozAppearance: "textfield",
        minHeight: "1.5em",
        width: "100%",
      },
      "& .MuiInputLabel-root": {
        fontSize: "0.875rem",
      },
      "& .MuiInputLabel-root.Mui-focused": {
        color: "#7e5bef",
      },
    }}
  />
);

export default CustomInput;
