"use client";
import { TextField, MenuItem } from "@mui/material";

const CustomMultiSelect = ({
  label,
  name,
  value = [],
  onChange,
  options = [],
  required,
  disabled,
}) => {
  const handleChange = (e) => {
    onChange({
      target: {
        name,
        value:
          typeof e.target.value === "string"
            ? e.target.value.split(",")
            : e.target.value,
      },
    });
  };

  return (
    <TextField
      select
      SelectProps={{ multiple: true }}
      size="small"
      variant="standard"
      label={label}
      name={name}
      value={value}
      onChange={handleChange}
      required={required}
      disabled={disabled}
      fullWidth
      margin="dense"
      sx={{
        width: "250px", // ✅ same width
        "& .MuiInputBase-root": {
          paddingBottom: "2px",
          transform: "translateY(2px)",
        },
        "& .MuiInput-input": {
          padding: "0px 0 1px",
          fontSize: "0.875rem",
        },
        "& .MuiInputLabel-root": {
          fontSize: "0.875rem",
        },
        "& .MuiInputLabel-root.Mui-focused": {
          color: "#7e5bef",
        },
      }}
    >
      {options.map((opt, idx) => (
        <MenuItem key={idx} value={opt}>
          {opt}
        </MenuItem>
      ))}
    </TextField>
  );
};

export default CustomMultiSelect;
