"use client";
import { TextField } from "@mui/material";

const CustomNumber = ({
    label,
    name,
    value,
    onChange,
    required,
    disabled,
    min,
    max,
    step,
}) => {
    const handleChange = (e) => {
        const val = e.target.value;
        if (val === "" || /^[0-9]*\.?[0-9]*$/.test(val)) {
            onChange(e);
        }
    };

    return (
        <TextField
            size="small"
            variant="standard"
            label={label}
            name={name}
            type="number"
            value={value || ""}
            onChange={handleChange}
            required={required}
            disabled={disabled}
            inputProps={{ min, max, step }}
            fullWidth
            margin="dense"
            sx={{
                width: "250px", // ✅ same width
                "& .MuiInputBase-root": {
                    fontSize: "0.875rem",
                    paddingBottom: "2px",
                },
                "& .MuiInputBase-input": {
                    padding: "6px 0 4px",
                },
                "& .MuiInputLabel-root": {
                    fontSize: "0.875rem",
                },
                "& .MuiInputLabel-root.Mui-focused": {
                    color: "#7e5bef",
                },
            }}
        />
    );
};

export default CustomNumber;
