"use client";
import { FormControlLabel, Switch } from "@mui/material";

const CustomSwitch = ({ label, name, value, onChange, disabled }) => {
  const checked = value === true || value === "true" || value === 1; // ✅ normalize

  return (
    <FormControlLabel
      control={
        <Switch
          checked={checked}
          onChange={(e) => onChange(name, e.target.checked)} // ✅ correctly updates boolean
          name={name}
          disabled={disabled}
          sx={{
            "& .MuiSwitch-switchBase.Mui-checked": {
              color: "#7e5bef",
            },
            "& .MuiSwitch-switchBase.Mui-checked + .MuiSwitch-track": {
              background: "linear-gradient(to right, #7e5bef, #00c6ff)",
            },
          }}
        />
      }
      label={label}
    />
  );
};

export default CustomSwitch;
