"use client";
import { TextField, MenuItem } from "@mui/material";

const CustomSelect = ({
  label,
  name,
  value,
  onChange,
  options = [],
  required,
  disabled,
}) => (
  <TextField
    select
    size="small"
    variant="standard"
    label={label}
    name={name}
    value={value || ""}
    onChange={(e) => onChange(name, e.target.value)}
    required={required}
    disabled={disabled}
    fullWidth
    margin="dense"
    sx={{
      width: "250px", // ✅ same width
      "& .MuiInputBase-root": {
        paddingBottom: "2px",
        transform: "translateY(2px)",
      },
      "& .MuiInput-input": {
        padding: "0px 0 1px",
        fontSize: "0.875rem",
      },
      "& .MuiInputLabel-root": {
        fontSize: "0.875rem",
      },
      "& .MuiInputLabel-root.Mui-focused": {
        color: "#7e5bef",
      },
    }}
  >
    {options.map((opt, idx) => (
      <MenuItem key={idx} value={opt}>
        {opt}
      </MenuItem>
    ))}
  </TextField>
);

export default CustomSelect;
